<script>
  export default{
    data(){
      return{
        workCardWrapperStyle: '', 
      }
    },
    methods:{
      updateCardSize(){
        var bodyWidth = document.body.clientWidth;
        var cardWidth = bodyWidth * 0.33;
        var cardHeight = cardWidth * 0.56;
        this.workCardWrapperStyle = 'width: ' + (cardWidth) + 'px; height: ' + (cardHeight) + 'px;';
      },
    },
    computed:{

    },
    created(){
      var bodyWidth = document.body.clientWidth;
      var cardWidth = bodyWidth * 0.33;
      var cardHeight = cardWidth * 0.56;
      this.workCardWrapperStyle = 'width: ' + (cardWidth) + 'px; height: ' + (cardHeight) + 'px;';
      this.$nextTick(function(){
       window.addEventListener('resize', this.updateCardSize);
      })
    },
    components:{

    },
    destroyed(){
      window.removeEventListener('resize', this.updateCardSize);
    }
  }
</script>